import React, { Component } from 'react'
import ReactDOM from 'react-dom'
import { Pagination, Input, Icon } from 'antd'
import axios from 'axios'
import PokemonItem from '../PokemonItem'
import 'antd/dist/antd.css'
import './Main.scss'

const { Search } = Input

export class Main extends Component {
  state = {
    value: '',
    isFindActive: false,
    foundedPokemons: [],
    options: { currentCount: 10, currentPage: 1 },
  }
  componentWillMount() {
    this.findPokemons()
  }
  componentDidMount() {
    setTimeout(() => this.startFind(), 100)
  }
  findPokemons = (page = 1, count = 10) => {
    this.setState({
      ...this.state,
      options: { currentCount: count, currentPage: 1 },
    })
    axios
      .get('https://pokeapi.co/api/v2/pokemon/', {
        params: {
          limit: count,
          offset: (page - 1) * count,
        },
      })
      .then(response => {
        response.data.results.pokemonsCount = response.data.count
        response.data.results.forEach((element, index) => {
          let temp = element.url.slice(0, -1)
          element.id = temp.slice(temp.lastIndexOf('/') + 1)
        })
        this.props.onChangePokemonArray(response.data)
      })
  }

  handleKeyDown = e => {
    if (!this.props.allPokemonsArray) this.startFind()
    if (e.keyCode === 13) this.findInArray(this.state.value)
  }

  startFind = () => {
    axios
      .get(
        `https://pokeapi.co/api/v2/pokemon/?limit=${this.props.countPokemons}`,
      )
      .then(response => {
        console.log('1234', response)
        this.props.onCreateAllPokemonsArray(response.data.results)
      })
  }

  findInArray = pokemon => {
    if (!this.props.allPokemonsArray) this.startFind()

    this.setState({ isFindActive: true })
    let tempPokemon = []
    this.props.allPokemonsArray.filter(el => {
      if (el.name.includes(pokemon) === true) {
        let temp = el.url.slice(0, -1)
        el.id = temp.slice(temp.lastIndexOf('/') + 1)
        tempPokemon.push(el)
        this.setState({ foundedPokemons: tempPokemon })
        const infoObj = {
          results: tempPokemon,
        }
        this.props.onChangePokemonArray(infoObj)
      }
    })
  }
  sortByAscending = () => {
    let tempPokemons = this.props.allPokemonsArray
    tempPokemons.sort((a, b) => {
      let nameA = a.name,
        nameB = b.name
      if (nameA < nameB) return -1
    })
    this.props.onCreateAllPokemonsArray(tempPokemons)
    this.setState({ ...this.state, foundedPokemons: tempPokemons })
    this.props.onChangePokemonArray({
      ...this.props.pokemonArray,
      results: tempPokemons,
    })
  }
  render() {
    const { pokemonArray, countPokemons, history } = this.props
    const { findInArray, handleKeyDown, sortByAscending } = this
    return (
      <div id="page">
        <div className="optionsBlock">
          <Search
            placeholder="input search text"
            onSearch={findInArray}
            enterButton
            onChange={e => this.setState({ value: e.target.value })}
            value={this.state.value}
            onKeyDown={handleKeyDown}
          />
          <div className="sortButtons">
            <Icon type="sort-ascending" onClick={sortByAscending} />
            <Icon type="sort-descending" />
          </div>
        </div>
        <div id="allItems">
          {pokemonArray.length ? (
            pokemonArray.map(items => (
              <PokemonItem key={items.id} source={items} history={history} />
            ))
          ) : (
            <div />
          )}
        </div>
        <Pagination
          ref={node => (this.pagination = ReactDOM.findDOMNode(node))}
          showSizeChanger
          onShowSizeChange={this.findPokemons}
          defaultCurrent={this.state.options.currentPage}
          total={countPokemons}
          pageSizeOptions={[10, 20, 50]}
          onChange={this.findPokemons}
        />
      </div>
    )
  }
}
